#root {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #f5f5f5;
  font-family: 'Open Sans', sans-serif;
  font-size: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: auto;
  flex-direction: column;
}

/* Пагинация по номера страниц */
.pagination-pg {
  display: flex;
  flex-direction: row;
}

.pagination-pg__item {
  padding: 10px 16px;
  background: #ffffff;
  margin-right: 8px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  border-radius: 2px;
  cursor: pointer;
}

.pagination-pg__item:last-of-type {
  margin-right: 0px;
}
/**/


/* Пагинация кнопками */
.pagination-btn {
  display: flex;
  flex-direction: row;
}

.pagination-btn__item {
  color: #ff9800;
  background: #ffffff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  border-radius: 2px;
  cursor: pointer;
  padding: 10px 32px;
  margin-left: 16px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.pagination-btn__item:first-of-type {
  margin-left: 0px;
}

.pagination-btn__finish  {
  color: #ffffff;
  background: #ff9800;
}

.pagination-btn__reset  {
  color: #ffffff;
  background: #ff9800;
  margin-top: 32px;
}
/**/


/* Кнопки */
.button--display-none {
  display: none;
}

.button--not-active {
  pointer-events: none;
  cursor: default;
  color: #bbbbbb;
}

.button--is-active {
  color: #ff9800;
}
/**/


/* Анкета */
.questionnaire {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  padding: 10px;
  box-sizing: border-box;
}

@media only screen and (max-width: 767px) {
    .questionnaire {
        width: 100%;
    }
    .pagination-btn__text {
      display: none;
    }
}

.questionnaire__description {
  margin-top: 16px;
  margin-bottom: 40px;
}

.questionnaire__data-input {
  display: flex;
  flex-direction: column;
  margin-bottom: 150px;
}
/**/


/* Форма */
.form {
  display: flex;
  flex-direction: column;
}

.form__item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 16px;
}

.form__item--space-between {
  justify-content: space-between;
}

.form__item:last-of-type {
  margin-bottom: 0px;
}

.form__social-network {
  margin-right: 16px;
}

.form__input {
  min-width: 280px;
  height: 40px;
  padding-left: 10px;
  font-size: 15px;
  border: 0px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  border-radius: 2px;
  cursor: pointer;
  background-color: #ffffff;
  outline:none;
}

.form__input--not-valid {
  outline: 2px solid red;
}

.form__input--hidden {
  visibility: hidden;
}

.form__error {
  color: red;
  margin-left: 16px;
}

.form__error--hidden {
  visibility: hidden;
}

.form__error--display-none {
  display: none;
}

.form__input--not-active {
  pointer-events: none;
  cursor: default;
  color: #bbbbbb;
}

.form__error--margin-top-20 {
  margin-top: 20px;
}
/**/


/* Плейсхолдеры для инпутов */
::-webkit-input-placeholder {color:#bbbbbb;}
::-moz-placeholder          {color:#bbbbbb;}
:-moz-placeholder           {color:#bbbbbb;}
:-ms-input-placeholder      {color:#bbbbbb;}

:focus::-webkit-input-placeholder {color: transparent}
:focus::-moz-placeholder          {color: transparent}
:focus:-moz-placeholder           {color: transparent}
:focus:-ms-input-placeholder      {color: transparent}
/**/


/* Блок картинки с животными*/
.animal-img {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.animal-img__item  {
  height: 150px;
  width: 150px;
  margin-right: 16px;
  margin-bottom: 16px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  border-radius: 2px;
  cursor: pointer;
}

.animal-img__item--is-choosed {
  outline: 3px solid #ff9800
}
/**/


/* Блок итоговой карточки */
.profile-card {
  padding: 24px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap-reverse;
}

.profile-card__information {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-right: 50px;
}

.profile-card__image {
  height: 195px;
  width: 200px;
}

.profile-card__personal {
  display: flex;
  flex-direction: column;
}

.profile-card__social-networks {
  display: flex;
  flex-direction: column;
}

.profile-card__name {
  font-size: 20px;
}

.profile-card__email {
  color: #bbbbbb;
  font-size: 15px;
}

.profile-card__location {
  color: #bbbbbb;
  font-size: 15px;
}
/**/
